body, *, *::after, *::before {
  padding: 0px;
  margin: 0px;
  position: relative;
  box-sizing: border-box;
}

body {
  background: #F5AA28;
}

.mainAllContainer {
  background: #F5AA28;
  height: 380px;
  width: 100vw;
  display: grid;
  align-items: center;
  justify-items: center;
  overflow: hidden;
  align-content: end;
}

.sceneMainWrapper {
  display: grid;
  align-items: center;
  justify-items: center;
  height: 200px;
  width: 100%;
  position: relative;
}
.sceneMainWrapper .sun {
  z-index: 99;
  position: absolute;
  height: 55px;
  width: 55px;
  background: #E51827;
  border-radius: 50%;
  right: 50px;
  top: -75px;
}
.sceneMainWrapper .cloud {
  position: absolute;
  height: 55px;
  width: 150px;
}
.sceneMainWrapper .cloud .row {
  background: inherit;
  height: 10px;
}
.sceneMainWrapper .cloud .row:nth-child(odd) {
  background: #F5AA28 !important;
}
.sceneMainWrapper .cloud .row:nth-child(even) {
  background: #F5C678 !important;
}
.sceneMainWrapper .cloud .row .c {
  height: 100%;
  position: absolute;
  top: 0px;
  border-radius: 5px;
}
.sceneMainWrapper .cloud1 {
  top: -15px;
  animation: Cloud 3s linear infinite;
  /* -------COLORS --------*/
}
.sceneMainWrapper .cloud1 .row1 .c1, .sceneMainWrapper .cloud1 .row1 .c2 {
  background: #F5C678;
}
.sceneMainWrapper .cloud1 .row1 .c1 {
  width: 40%;
  left: 15px;
}
.sceneMainWrapper .cloud1 .row1 .c2 {
  width: 15px;
  right: 50px;
}
.sceneMainWrapper .cloud1 .row2 .c3, .sceneMainWrapper .cloud1 .row2 .c4 {
  background: #F5AA28;
}
.sceneMainWrapper .cloud1 .row2 .c3 {
  width: 30px;
  left: -5px;
}
.sceneMainWrapper .cloud1 .row2 .c4 {
  width: 100px;
  left: 65px;
}
.sceneMainWrapper .cloud1 .row3 .c5, .sceneMainWrapper .cloud1 .row3 .c6 {
  background: #F5C678;
}
.sceneMainWrapper .cloud1 .row3 .c5 {
  width: 80%;
  left: 0px;
}
.sceneMainWrapper .cloud1 .row3 .c6 {
  width: 20px;
  right: 0px;
}
.sceneMainWrapper .cloud1 .row4 .c7, .sceneMainWrapper .cloud1 .row4 .c8 {
  background: #F5AA28;
}
.sceneMainWrapper .cloud1 .row4 .c7 {
  width: 50px;
  left: -15px;
}
.sceneMainWrapper .cloud1 .row4 .c8 {
  width: 100px;
  left: 90px;
}
.sceneMainWrapper .cloud1 .row5 .c9, .sceneMainWrapper .cloud1 .row5 .c10 {
  background: #F5C678;
}
.sceneMainWrapper .cloud1 .row5 .c9 {
  width: 65%;
  left: 10px;
}
.sceneMainWrapper .cloud1 .row5 .c10 {
  width: 15px;
  right: 15px;
}
.sceneMainWrapper .cloud2 {
  right: -200px;
  top: -140px;
  animation: Cloud 3s linear 2s infinite;
}
.sceneMainWrapper .cloud2 .row1 .c1, .sceneMainWrapper .cloud2 .row1 .c2 {
  background: #F5C678;
}
.sceneMainWrapper .cloud2 .row1 .c1 {
  width: 40%;
  left: 40px;
}
.sceneMainWrapper .cloud2 .row1 .c2 {
  width: 15px;
  right: 25px;
}
.sceneMainWrapper .cloud2 .row2 .c3, .sceneMainWrapper .cloud2 .row2 .c4 {
  background: #F5AA28;
}
.sceneMainWrapper .cloud2 .row2 .c3 {
  width: 55px;
  left: -3px;
}
.sceneMainWrapper .cloud2 .row2 .c4 {
  width: 100px;
  left: 90px;
}
.sceneMainWrapper .cloud2 .row3 .c5, .sceneMainWrapper .cloud2 .row3 .c6 {
  background: #F5C678;
}
.sceneMainWrapper .cloud2 .row3 .c5 {
  width: 80%;
  left: 0px;
}
.sceneMainWrapper .cloud2 .row3 .c6 {
  width: 20px;
  right: 0px;
}
.sceneMainWrapper .cloud2 .row4 .c7, .sceneMainWrapper .cloud2 .row4 .c8 {
  background: #F5AA28;
}
.sceneMainWrapper .cloud2 .row4 .c7 {
  width: 50px;
  left: -10px;
}
.sceneMainWrapper .cloud2 .row4 .c8 {
  width: 100px;
  left: 85px;
}
.sceneMainWrapper .cloud2 .row5 .c9, .sceneMainWrapper .cloud2 .row5 .c10 {
  background: #F5C678;
}
.sceneMainWrapper .cloud2 .row5 .c9 {
  width: 50%;
  left: 20px;
}
.sceneMainWrapper .cloud2 .row5 .c10 {
  width: 15px;
  right: 30px;
}

.carBodyContainerBase {
  height: 60px;
  width: 120px;
}

.carMidMainBody {
  z-index: 99;
  position: absolute;
  height: 100%;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.carMidMainBody .upperPart, .carMidMainBody .lowerPart {
  position: absolute;
  width: 100%;
  left: 0px;
}
.carMidMainBody .upperPart {
  top: 0px;
  width: 100%;
  height: 30px;
  background: #F5DAB4;
  border: 2.5px solid #820D2B;
  display: grid;
  grid-template-columns: auto auto auto;
  grid-template-rows: 100%;
  grid-column-gap: 5px;
  border-top-left-radius: 16px;
  border-top-right-radius: 30px;
  padding: 4px;
  overflow: hidden;
  animation: BodyShakeUpper 0.6s linear infinite;
}
.carMidMainBody .upperPart .window {
  background: #820D2B;
  height: 18px;
  border-radius: 3px;
}
.carMidMainBody .upperPart .window1 {
  width: 24px;
  border-top-left-radius: 9px;
}
.carMidMainBody .upperPart .window2 {
  width: 38px;
}
.carMidMainBody .upperPart .window3 {
  width: 25px;
  border-top-right-radius: 100px 70px;
}
.carMidMainBody .upperPart .windowSlider {
  z-index: 5;
  position: absolute;
  right: 12px;
  top: -2px;
  height: 28px;
  width: 5px;
  background: #f3dfc4;
  transform: rotate(-30deg);
  animation: WindowSlider 2s linear infinite;
}
.carMidMainBody .upperPart .windowSlider::after {
  content: " ";
  position: absolute;
  top: 4px;
  height: 30px;
  width: 1px;
  right: -4px;
  background: #f3dfc4;
}
.carMidMainBody .upperPart .line1 {
  z-index: 9;
  position: absolute;
  left: 31px;
  bottom: 0;
  height: 20px;
  width: 2.5px;
  background: #820D2B;
}
.carMidMainBody .upperPart .line2 {
  z-index: 9;
  position: absolute;
  right: 35px;
  bottom: 0;
  height: 16px;
  width: 2.5px;
  background: #820D2B;
}
.carMidMainBody .lowerPart {
  top: 30px;
  height: 35px;
  width: 100%;
  background: #E51827;
  border: 2.5px solid #820D2B;
  border-top: none;
  animation: BodyShakeLower 0.6s linear infinite;
  /**** Guard *****/
  /**** BUMPER *****/
  /**** Ladder *****/
  /**** Headlight *****/
}
.carMidMainBody .lowerPart .Llines {
  position: absolute;
  height: 2.5px;
  background: #820D2B;
  top: 1.5px;
}
.carMidMainBody .lowerPart .lines1 {
  left: 3px;
  width: 25px;
}
.carMidMainBody .lowerPart .lines2 {
  left: 32px;
  width: 18px;
}
.carMidMainBody .lowerPart .lines3 {
  left: 55px;
  width: 6px;
}
.carMidMainBody .lowerPart .lines4 {
  right: 17px;
  width: 34px;
}
.carMidMainBody .lowerPart .lines5 {
  right: 4px;
  width: 11px;
}
.carMidMainBody .lowerPart .lines6 {
  top: 8px;
  right: 24px;
  width: 8px;
}
.carMidMainBody .lowerPart .lines7 {
  top: 8px;
  right: 40px;
  width: 8px;
}
.carMidMainBody .lowerPart .Vlines {
  position: absolute;
  width: 2.5px;
  background: #820D2B;
}
.carMidMainBody .lowerPart .Vline1 {
  right: 35px;
  height: 7px;
  top: 6px;
}
.carMidMainBody .lowerPart .Vline2 {
  top: 16px;
  right: 35px;
  height: 7px;
}
.carMidMainBody .lowerPart .Vline3 {
  top: 6px;
  left: 31px;
  height: 16px;
}
.carMidMainBody .lowerPart .Guard {
  position: absolute;
  width: 30px;
  bottom: 0px;
  background: #F5AA28;
}
.carMidMainBody .lowerPart .GuardLeft {
  bottom: -9px;
  left: 12px;
  height: 14px;
  background: #F5AA28;
  border: 2.5px solid #820D2B;
  border-bottom: none;
  border-radius: 6px;
}
.carMidMainBody .lowerPart .GuardLeft::after, .carMidMainBody .lowerPart .GuardLeft::before {
  position: absolute;
  content: " ";
  background: #F5AA28;
  height: 7px;
  width: 10px;
  bottom: -1px;
}
.carMidMainBody .lowerPart .GuardLeft::after {
  left: -5px;
}
.carMidMainBody .lowerPart .GuardLeft::before {
  right: -5px;
}
.carMidMainBody .lowerPart .GuardLeftFix {
  z-index: 9;
  position: absolute;
  bottom: 2px;
  left: 18px;
  height: 8px;
  border-bottom: 2.5px solid #820D2B;
  width: 20px;
  background: #E51827;
}
.carMidMainBody .lowerPart .GuardRight {
  right: 12px;
  width: 30px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  bottom: -18px;
  border: 2.5px solid #820D2B;
}
.carMidMainBody .lowerPart .GuardRight::after {
  position: absolute;
  content: " ";
  height: 14px;
  width: 38px;
  background: #F5AA28;
  bottom: -1px;
  left: -8px;
}
.carMidMainBody .lowerPart .Bumper {
  height: 10px;
  position: absolute;
  background: #F5DAB4;
  border: 2.5px solid #820D2B;
  border-radius: 5px;
}
.carMidMainBody .lowerPart .BumperFront {
  z-index: 2;
  bottom: -5px;
  width: 14px;
  right: -6px;
}
.carMidMainBody .lowerPart .BumperRear {
  width: 16px;
  bottom: -5px;
  left: -8px;
}
.carMidMainBody .lowerPart .ladder {
  position: absolute;
  height: 48px;
  width: 9px;
  bottom: 6px;
  left: -13px;
  border: 2.5px solid #820D2B;
  border-bottom: none;
  border-left: none;
  border-top-right-radius: 10px;
}
.carMidMainBody .lowerPart .ladder .step {
  position: absolute;
  width: 6px;
  height: 2.5px;
  background: #820D2B;
  right: -6px;
}
.carMidMainBody .lowerPart .ladder .step1 {
  bottom: 5px;
}
.carMidMainBody .lowerPart .ladder .step2 {
  bottom: 20px;
}
.carMidMainBody .lowerPart .ladder .step3 {
  bottom: 35px;
}
.carMidMainBody .lowerPart .headlight {
  background: #B56767;
  z-index: 1;
  position: absolute;
  height: 34px;
  width: 11px;
  right: -12px;
  border: 2.5px solid #820D2B;
  border-radius: 8px;
  bottom: 2px;
  overflow: hidden;
}
.carMidMainBody .lowerPart .headlight .lightMid {
  height: 100%;
  position: absolute;
  width: 1px;
  background: #fff;
  left: 50%;
  top: 0px;
  transform: translateX(-50%);
}
.carMidMainBody .lowerPart .headlight .dot {
  z-index: 9;
  position: absolute;
  width: 5px;
  height: 5px;
  background: #820D2B;
}
.carMidMainBody .lowerPart .headlight .dot1 {
  left: -3px;
  bottom: 2px;
}
.carMidMainBody .lowerPart .headlight .dot2 {
  right: -3px;
  bottom: 0px;
}
.carMidMainBody .lowerPart .headlight .dot3 {
  right: 0px;
  top: 13px;
}
.carMidMainBody .lowerPart .headlight .dot4 {
  left: -3px;
  top: 2px;
}
.carMidMainBody .topRoof {
  width: 68%;
  height: 6px;
  position: absolute;
  top: -4px;
  left: 45%;
  transform: translateX(-50%);
  border-top: 2.5px solid #820D2B;
  animation: BodyShakeTopRoof 0.6s linear infinite;
}
.carMidMainBody .topRoof::after, .carMidMainBody .topRoof::before {
  content: " ";
  position: absolute;
  width: 2.5px;
  height: 4px;
  background: #820D2B;
}
.carMidMainBody .topRoof::after {
  left: 15px;
}
.carMidMainBody .topRoof::before {
  right: 20px;
}

.wheel {
  z-index: 8;
  position: absolute;
  height: 24px;
  width: 24px;
  border-radius: 50%;
  background: #820D2B;
  animation: Wheels 0.8s linear infinite;
}
.wheel .innerCircle {
  height: 12px;
  width: 12px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #F5AA28;
  border-radius: 50%;
}
.wheel .innerRadius {
  height: 9px;
  width: 9px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  border: 2px solid #820D2B;
  background: #F5DAB4;
}
.wheel .innerRadius::after {
  position: absolute;
  top: -1px;
  left: -2px;
  content: "";
  height: 3px;
  width: 3px;
  background: #fff;
  border-radius: 50%;
}

.wheelLeft {
  left: 17px;
  bottom: -20px;
}

.wheelRight {
  right: 17px;
  bottom: -20px;
}

.road {
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
  bottom: -35.5px;
  height: 15px;
  width: 200%;
  border-left: none;
  border-right: none;
  overflow: hidden;
}
.road .roadGap {
  position: absolute;
  height: 5px;
  background: #F5AA28;
  background: #820D2B;
  border-radius: 3px;
  border-radius: 3px;
}
.road .roadGap1 {
  right: 0px;
  top: 0px;
  width: 30px;
}
.road .roadGap2 {
  left: 50%;
  transform: translateX(-50%);
  top: 0px;
  width: 220px;
}
.road .roadGap3 {
  left: 0px;
  top: 0px;
  width: 30px;
}
.road .roadGap4 {
  left: 45px;
  bottom: 0px;
  width: 30px;
}
.road .roadGap5 {
  left: 82px;
  bottom: 0px;
  width: 65px;
}
.road .roadGap6 {
  left: 155px;
  bottom: 0px;
  width: 65px;
}
.road .roadGap7 {
  right: 40px;
  bottom: 0px;
  width: 30px;
}
.road .RoadAnimation1 {
  width: 10px;
  top: 0px;
  left: 105%;
  background: #F5AA28;
  width: 10px;
  border-radius: 0px;
  animation: Road1 2s linear infinite;
}
.road .RoadAnimation2 {
  width: 10px;
  bottom: 0px;
  left: 90%;
  background: #F5AA28;
  border-radius: 0px;
  animation: Road2 2s linear infinite;
}

/* -------ANIMATIONS KEYFRAMES---------*/
@keyframes Cloud {
  0% {
    left: 110%;
    left: calc(100% + 150px);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    left: -150px;
    opacity: 0;
  }
}
@keyframes Wheels {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes Road1 {
  to {
    left: -5%;
  }
}
@keyframes Road2 {
  to {
    left: 10%;
  }
}
@keyframes BodyShakeUpper {
  0% {
    top: 0px;
  }
  50% {
    top: -3px;
  }
  100% {
    top: 0px;
  }
}
@keyframes BodyShakeLower {
  0% {
    top: 30px;
  }
  50% {
    top: 27px;
  }
  100% {
    top: 30px;
  }
}
@keyframes BodyShakeTopRoof {
  0% {
    top: -4px;
  }
  50% {
    top: -7px;
  }
  100% {
    top: -4px;
  }
}
@keyframes WindowSlider {
  to {
    right: 100%;
  }
}
